<template>
  <div class="scroll"
       ref="scrollWrapper">
    <!--  -->
    <slot></slot>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
  name: 'scroll',
  data() {
    return {}
  },
  props: {
    data: {
      type: String,
      default() {
        return ''
      }
    }
  },
  watch: {
    data(val) {
      //datarefresh
      this.$nextTick(() => {
        this.refresh()
      })
    }
  },
  mounted() {
    //dominitScroll
    this.$nextTick(() => {
      this.initScroll()
    })
  },
  methods: {
    initScroll() {
      if (!this.$refs.scrollWrapper) return
      this.scroll = new BScroll(this.$refs.scrollWrapper, {
        //
        click: true
      })
    },
    refresh() {
      //domrefreshdom
      this.scroll && this.scroll.refresh()
    },
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
    },
    scrollTo() {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
    }
  }
}
</script>

